// Ermilov

@font-face {
    font-family: 'Ermilov';
    src:    url('@fonts/Ermilov-Bold.woff2') format('woff2'),
            url('@fonts/Ermilov-Bold.woff') format('woff'),
            // url('@fonts/Ermilov-Bold.otf') format('otf'),
            url('@fonts/Ermilov-Bold.ttf') format('ttf');
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

// Ermilov END

