@import "@theme/vars.scss";


.section-bitrix {
    overflow: hidden;
    background-color: #151515;
    padding-bottom: 0;

    &__header{
        margin-bottom: 100px;
        padding: 0 25px;
    }

    &__title {
        display: flex;
        flex-direction: column;
        color: #fff;

        span{
            display: inline-flex;
        }

        strong{
            position: relative;
            color: $red;
            width: 63px;
            transition: width 0.5s ease 0.25s;

            span{
                position: absolute;
                top: 0;
                left: 0;
                opacity: 0;

                &:nth-of-type(1){
                    opacity: 1;
                    transition: opacity 0.5s ease 0.25s;
                }
    
                &:nth-of-type(2){
                    opacity: 0;
                    transition: opacity 0.5s ease;
                }
            }
            
            &.is-swapped{
                width: 115px;
                transition-delay: 0s;

                span{
                    &:nth-of-type(1){
                        opacity: 0;
                        transition-delay: 0s;
                    }
        
                    &:nth-of-type(2){
                        opacity: 1;
                        transition-delay: 0.25s;
                    }
                }
            }
        }
    }

    &__inner {
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        justify-content: space-between;
    }

    &__item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        padding: 26px 30px;
        color: #fff;

        &:nth-of-type(1){
            justify-content: flex-start;
            padding: 30px;
            min-height: 454px;
            min-width: 304px;
            width: 78%;
            border: 1px solid #595959;
            padding-bottom: 300px;
            background-size: 100px;
            background-repeat: repeat;
            background-position: left top;
            background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_364_507)'%3E%3Cg clip-path='url(%23clip1_364_507)'%3E%3Cg opacity='0.8'%3E%3Crect x='-154.462' y='90.0581' width='0.628112' height='531' transform='rotate(-45 -154.462 90.0581)' fill='%23595959'/%3E%3Crect x='-137.754' y='73.3506' width='0.628112' height='531' transform='rotate(-45 -137.754 73.3506)' fill='%23595959'/%3E%3Crect x='-121.047' y='56.6428' width='0.628112' height='531' transform='rotate(-45 -121.047 56.6428)' fill='%23595959'/%3E%3Crect x='-104.339' y='39.9353' width='0.628112' height='531' transform='rotate(-45 -104.339 39.9353)' fill='%23595959'/%3E%3Crect x='-87.6316' y='23.2278' width='0.628112' height='531' transform='rotate(-45 -87.6316 23.2278)' fill='%23595959'/%3E%3Crect x='-70.9238' y='6.52002' width='0.628112' height='531' transform='rotate(-45 -70.9238 6.52002)' fill='%23595959'/%3E%3Crect x='-54.2163' y='-10.1875' width='0.628112' height='531' transform='rotate(-45 -54.2163 -10.1875)' fill='%23595959'/%3E%3Crect x='-37.5088' y='-26.895' width='0.628112' height='531' transform='rotate(-45 -37.5088 -26.895)' fill='%23595959'/%3E%3Crect x='-20.801' y='-43.6028' width='0.628112' height='531' transform='rotate(-45 -20.801 -43.6028)' fill='%23595959'/%3E%3Crect x='-4.09351' y='-60.3103' width='0.628112' height='531' transform='rotate(-45 -4.09351 -60.3103)' fill='%23595959'/%3E%3Crect x='12.614' y='-77.0178' width='0.628112' height='531' transform='rotate(-45 12.614 -77.0178)' fill='%23595959'/%3E%3Crect x='29.3218' y='-93.7256' width='0.628112' height='531' transform='rotate(-45 29.3218 -93.7256)' fill='%23595959'/%3E%3Crect x='46.0293' y='-110.433' width='0.628112' height='531' transform='rotate(-45 46.0293 -110.433)' fill='%23595959'/%3E%3Crect x='62.7368' y='-127.141' width='0.628112' height='531' transform='rotate(-45 62.7368 -127.141)' fill='%23595959'/%3E%3