        }

        
        &:nth-of-type(2){
            min-width: 285px;
            width: 83%;
            margin-left: -100%;
        }

        
        &:nth-of-type(5n - 4){
            min-height: 224px;
            border: 1px solid #595959;
        }

        &:nth-of-type(5n - 3){
            min-height: 234px;
            background-color: $yellow;
            color: $gray;
        }

        &:nth-of-type(5n - 2){
            min-height: 224px;
        }

        &:nth-of-type(5n - 1){
            min-height: 212px;
            background-color: $yellow;
            color: $gray;
        }

        &:nth-of-type(5n){
            padding: 26px 32px;
            min-height: 200px;
            border: 1px solid #595959;
            background-size: 100px;
            background-repeat: repeat;
            background-position: left top;
            background-image: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.8'%3E%3Crect x='9.94189' y='-154.462' width='0.628112' height='531' transform='rotate(45 9.94189 -154.462)' fill='%23595959'/%3E%3Crect x='26.6494' y='-137.754' width='0.628112' height='531' transform='rotate(45 26.6494 -137.754)' fill='%23595959'/%3E%3Crect x='43.3569' y='-121.047' width='0.628112' height='531' transform='rotate(45 43.3569 -121.047)' fill='%23595959'/%3E%3Crect x='60.0647' y='-104.339' width='0.628112' height='531' transform='rotate(45 60.0647 -104.339)' fill='%23595959'/%3E%3Crect x='76.7722' y='-87.6316' width='0.628112' height='531' transform='rotate(45 76.7722 -87.6316)' fill='%23595959'/%3E%3Crect x='93.48' y='-70.9238' width='0.628112' height='531' transform='rotate(45 93.48 -70.9238)' fill='%23595959'/%3E%3Crect x='110.188' y='-54.2163' width='0.628112' height='531' transform='rotate(45 110.188 -54.2163)' fill='%23595959'/%3E%3Crect x='126.895' y='-37.5088' width='0.628112' height='531' transform='rotate(45 126.895 -37.5088)' fill='%23595959'/%3E%3Crect x='143.603' y='-20.801' width='0.628112' height='531' transform='rotate(45 143.603 -20.801)' fill='%23595959'/%3E%3Crect x='160.31' y='-4.09351' width='0.628112' height='531' transform='rotate(45 160.31 -4.09351)' fill='%23595959'/%3E%3Crect x='177.018' y='12.614' width='0.628112' height='531' transform='rotate(45 177.018 12.614)' fill='%23595959'/%3E%3Crect x='193.725' y='29.3218' width='0.628112' height='531' transform='rotate(45 193.725 29.3218)' fill='%23595959'/%3E%3Crect x='210.433' y='46.0293' width='0.628112' height='531' transform='rotate(45 210.433 46.0293)' fill='%23595959'/%3E%3Crect x='227.141' y='62.7368' width='0.628112' height='531' transform='rotate(45 227.141 62.7368)' fill='%23595959'/%3E%3Crect x='243.848' y='79.4446' width='0.628112' height='531' transform='rotate(45 243.848 79.4446)' fill='%23595959'/%3E%3Crect x='260.556' y='96.1521' width='0.628112' height='531' transform='rotate(45 260.556 96.1521)' fill='%23595959'/%3E%3C/g%3E%3C/svg%3E%0A");
        }
        
    }

    &__item:nth-of-type(5n - 3) &__item-text,
    &__item:nth-of-type(5n - 1) &__item-text {
        color: $gray;
    }

    &__item-content {
    }

    &__item-title {
        text-transform: initial;
        margin-bottom: 15px;
    }

    &__item-text {
        color: #F0F0F0;
    }
}