@import '@theme/vars.scss';

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

p{
    font-weight: 400;
}


.h1, .h2, .h3, .h4, .h5, .h6 {
    text-transform: uppercase;
    font-family: 'Ermilov';
    font-weight: 700;
}

.h1{
    font-size: 40px;
    line-height: 110%;
}

.h2{
    font-size: 25px;
    line-height: 145%;
}

.h3{
    font-size: 20px;
    line-height: 26px;
}

.h4{
    font-size: 16px;
    line-height: 145%;
    letter-spacing: 0.01em
}

.h5{
    font-size: 14px;
    line-height: 145%;
    letter-spacing: 0.01em
}

.text{
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.15px;

    &-xl{
        font-size: 20px;
        line-height: 140%;
    }

    &-lg{
        font-size: 18px;
        line-height: 140%;
    }

    &-md{
        font-size: 14px;
        line-height: 140%;
    }

    &-sm{
        font-size: 12px;
        line-height: 140%;
    }

    //STYLE

    &-border{
        -webkit-text-stroke: 1px #8C8C8C;
    }

    //WEIGHT

    &-light{
        font-weight: 300;
    }

    &-regular{
        font-weight: 400;
    }
    
    &-medium{
        font-weight: 500;
    }

    &-semibold{
        font-weight: 600;
    }

    &-bold{
        font-weight: 700;
    }

    //FAMILY


    &-plex{
        font-family: 'IBMPlexMono';
    }

    &-ermilov{
        font-family: 'Ermilov';
    }

    &-roboto{
        font-family: 'Roboto';
    }
}