@import "@theme/vars.scss";

.input{
    box-sizing: border-box;
    background: none;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    width: 100%;
    color: $blue;
    padding: 0 8px 8px;
    font-weight: 400;
    font-family: "Inter";
    font-size: 14px;
    background-color: transparent;
    border-bottom: 3px solid $blue;
    transition: background-color 0.35s ease 0.1s, border-color 0.35s ease, color 0.35s ease;

    &::placeholder{
        color: inherit;
        opacity: 0.5;
    }

    &.is-error{
        border-color: #F6537A;
    }

	&.is-focus{
		border-color: $blue;
	}

}