@import "@theme/vars.scss";

.section-reasons {
    position: relative;
    background-color: $dark;
    padding-bottom: 0;
    height: 888px;
    overflow: hidden;

    &__header {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        position: absolute;
        top: 95px;
        right: 20px;
    }

    &__title {
        display: inline-flex;
        flex-direction: column;
        align-items: flex-end;
        text-align: right;
        line-height: 35px;

        span{
                
        }
    }

    
    &__title-icon{
        width: 43px;
        height: 65px;
        margin-left: 5px;
    }

    &__inner {
    }

    &__list {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        overflow: hidden;
        height: 792px;
    }

    &__item {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        padding: 40px;
        height: 492px;
        height: 100%;
        flex-shrink: 0;
        overflow: hidden;
        transition: transform 0.5s ease;
     

        &:nth-of-type(1){
            background-color: $red;
            color: #fff;
            margin-top: 0 !important;
        }

        
        &:nth-of-type(2){
            background-color: $yellow;
            color: $gray;
            top: 100px;
        }
        
        &:nth-of-type(3){
            background-color: $blue;
            color: #fff;
            top: 200px;
        }
        
        &:nth-of-type(4){
            background-color: #fff;
            color: $gray;
            top: 300px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        &:hover{
            margin-bottom: 0;
            // transform: translateY(392px);
        }
    }

    &__item:last-of-type &__item-body{
        opacity: 1;
    }

    &__item:hover &__item-body{
        opacity: 1;
        pointer-events: all;
        transition: opacity 0.5s ease 0.25s;
    }

    &__item:hover ~ &__item{
        transform: translateY(392px) !important;
    }

    &__item-header{
        margin-bottom: 25px;
    }

    &__item-title{
        text-transform:capitalize;

        img{
            height: 66px;
            width: auto;
            margin: 0 -8px;
        }
    }

    &__item-body {
        box-sizing: border-box;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.5s ease;
    }

    &__item-content{
    }


    &__item-text {
        -webkit-line-clamp: 12;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
}