@import "@theme/vars.scss";

.card-review {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
    min-height: 400px;

    &__header {
        display: flex;
        margin-bottom: 20px;
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        flex-shrink: 0;
        margin-right: 5px;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__header-main {
    }

    &__name {
        color: $gray;
        line-height: 23px;
    }

    &__project {
        line-height: 18px;
        color: #595959;
    }

    &__body {
        margin-bottom: auto;
    }

    &__body-content {
    }

    &__text {
        color: #595959;
    }

    &__footer {
        margin-top: 20px;
    }

    &__link {
    }
}