@import "@theme/vars.scss";

.olympia {
    background-color: #0D0D0D;
    color: #fff;
    font-family: 'Roboto';

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
        color: #fff;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 30px 20px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex-grow: 1;
    }

    &__intro-logo {
        width: 100%;
        max-width: 440px;
        margin-bottom: 25px;
    }

    &__intro-subtitle {
        text-align: center;
        margin-bottom: 25px;
        line-height: 150%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -10px -20px;
    }

    &__intro-item {
        display: flex;
        flex-direction: column;
        margin: 0 10px 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        font-size: 10px;
        line-height: 12px;
        margin-bottom: 10px;

        &:last-child{
            font-weight: 500;
            margin-bottom: 0;
        }
    }

    &__intro-item-text{
        font-size: 12px;
        line-height: 13px;
        display: flex;
    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 60px 20px;

        &--typography{
        }

        &--1{
            padding-bottom: 0;
            overflow: hidden;
            background-color: $gray;

            &::after{
                content: "";
                width: 100%;
                height: 140px;
                background-color: #0D0D0D;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }

        &--2 {
            flex-direction: row;
            padding: 0;
            height: 700px;
            overflow: hidden;
        }

        &--3{
        }

        &--4{
            padding: 0;
        }
    }

    &__screen--1 &__img{

        &--1{
            width: 93.5%;
        }

        &--2{
            width: 20.5%;
            position: absolute;
            top: 165px;
            right: 20px;
        }
    }
    
    &__screen--2 &__img{
        height: 100%;
        object-position: center top;
    }

    &__screen--3 &__img{
        width: 96%;

        &--1{
            margin-bottom: 18px;
        }

        &--2{
            // width: 96%;
        }

        &--3{
            position: absolute;
            top: 165px;
            top: 50%;
            transform: translate(50%, -50%);
            right: 0;
        }
    }
        
    &__screen--4 &__img{
        width: 100%;
        object-position: center center;
    }      

    &__screen--4 &__header{
        padding-right: 20px;
        padding-left: 20px;
    }

    &__screen-inner{
        position: relative;
    }

    &__img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
        z-index: 1;
    }

    &__header {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 40px;
    }

    &__header-title {
        color: #CCCCCC;
    }

    &__header-subtitle {
        color: #7E7E7E;
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__typography-img {
        display: block;
        width: calc(100% - 64px);
        max-width: 392px;
        object-fit: contain;
        margin: 0 32px 40px;
    }

    &__typography-colors {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }

    &__typography-color {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        border-radius: 50%;
        flex-shrink: 0;
        transform-origin: center bottom;

        span{
            font-size: 8px;
            line-height: 10px;
        }

        &:nth-of-type(1){
            width: 132px;
            height: 132px;
            background-color: #EB5757;
        }

        &:nth-of-type(2){
            width: 108px;
            height: 108px;
            background-color: $gray;
            margin-left: -28px;
        }

        &:nth-of-type(3){   
            height: 90px;
            width: 90px;
            background-color: #191919;
            margin-left: -16px;
        }

    }

}
