@import "@theme/vars.scss";

.section-404 {
    height: 100vh;
    overflow: hidden;
    background-color: #fff;

    &__inner {
    }

    &__bg {
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        align-items: center;
        flex-shrink: 0;
        width: 100%;
        aspect-ratio: 1.06;
        max-height: 450px;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__main {
        margin: 0 auto;
        width: 100%;
        box-sizing: border-box;
        padding: 45px 20px;
        max-width: 520px;
    }

    &__title {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        color: $gray;

        img{
            display: inline-flex;
            height: 42px;
            width: 86px;
            flex-shrink: 0;
            margin-right: 10px;
        }

        span{
            &:first-of-type {
                width: 100%;
            }
        }

        strong{
            width: 100%;
            color: transparent;
            -webkit-text-stroke: 1px $gray;
        }
    }

    &__button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: 128px;
        height: 128px;
        background-color: #D95B4F;
        text-decoration: none;
        color: #fff;
        font-size: 12px;
        line-height: 11px;
        transform: translateY(-50%);
        margin-left: auto;
    }
}