@import "@theme/vars.scss";

.richness {
    background-color: #333333;
    color: #fff;
    padding-bottom: 100px;

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
        color: #fff;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 30px 20px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        flex-grow: 1;
    }

    &__intro-logo {
        width: 269px;
        height: 37px;
        margin-bottom: 18px;
    }

    &__intro-subtitle {
        margin-bottom: 18px;
        line-height: 150%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -20px -20px;
    }

    &__intro-item {
        margin: 0 20px 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        margin-bottom: 10px;

        &:first-child{
            font-weight: 700;
            color: #DABE83;
        }

        &:last-child{
            font-weight: 500;
            margin-bottom: 0;
        }
    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: flex;
        flex-direction: column;

        &--typography{
            padding-top: 60px;
        }

        &--1{
            flex-direction: row;
            overflow: hidden;
            height: 722px;
            background-color: #DABE83;
        }

        &--2 {
            padding: 100px 20px;
        }

        &--3{
            height: 100vh;
        }

        &--4{
            padding: 60px 0 0;
            margin-bottom: 100px;
            align-items: center;
            background-color: #fff;

            &::after{
                content: "";
                width: 100%;
                height: 64px;
                background-color: #333333;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }
    }

    &__screen--typography &__haeder{
        margin-bottom: 150px;
    }

    &__screen--1 &__img{
        height: 722px;
        width: auto;
        object-position: top center;
        object-fit: cover;
    }

    &__screen--2 &__img{
        box-sizing: border-box;
        width: 100%;
        max-height: 100vh;
        margin-bottom: -18%;
        object-fit: contain;
    }

    &__screen--3 &__img{
        height: 100%;
        width: auto;
        min-width: 130%;
        max-width: none;
        object-fit: cover;
    }

    &__screen--4 &__img{
        position: relative;
        z-index: 1;
        object-fit: contain;

        &--1{
            position: absolute;
            width: 204px;
            height: 177px;
            top: 86px;
            right: -30px;
        }

        &--2{
            width: 120%;
            margin: -3% -20% -18% -1%;
        }
    }

    &__screen--4 &__img-wrapper{
        z-index: 2;
        width: calc(100% - 20px);
    }
    
    &__screen--5 &__img{
        position: relative;
        z-index: 1;
        object-fit: contain;

        &--1{
            width: 131%;
            margin: -5% -31% -26% -1%;
            // transform: translateX(24%);
        }

        &--2{
            width: 131%;
            margin: -5% -31% -27% -1%;
            // transform: translateX(-24%);
        }
    }

    &__screen--5 &__img-wrapper{
        margin-bottom: 100px;

        &:last-of-type{
            margin-bottom: 0;
        }

        &--1{
            transform: translateX(52%);
        }

        &--2{
            transform: translateX(-24%);
        }
    }

    &__screen--4 &__header{
        color: $gray
    }

    &__img-wrapper{
        display: flex;
    }

    &__img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
    }

    &__header {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        padding: 0 20px;
        margin-bottom: 100px;
        position: relative;
        z-index: 2;
        width: 100%;
    }

    &__header-title {
    }

    &__header-number {
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__typography-img {
        display: block;
        width: 100%;
        max-width: 392px;
        object-fit: contain;
        margin-bottom: 70px;
    }

    &__typography-colors {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__typography-color {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 50%;
        height: 208px;
        background-color: #fff;
        color: $gray;

        span{
            margin-bottom: 10px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        &:nth-of-type(1){
            background-color: #333;
            color: #BFBFBF;
        }

        &:nth-of-type(2){
            background-color: #fff;
        }

        &:nth-of-type(3){   
            background-color: #292929;
            color: #BFBFBF;
        }

        &:nth-of-type(4){
            background-color: #DABE83;
        }
    }

}
