@import "@theme/vars.scss";

.section-services {

    &__list {
        position: relative;
        height: 554px;
    }

    &__item {
        position: absolute;


        &:nth-of-type(1){
            background-color: $red;
            top: 0;
            right: 0;
            z-index: 0;
            min-width: 320px;
            width: 91.4%;
            height: 550px;
        }
        
        &:nth-of-type(2){
            background-color: $yellow;
            color: $gray;
            top: 90px;
            left: 0;
            z-index: 1;
            width: 94.2%;
            min-width: 330px;
            height: 460px;

            .card-service__close::after,
            .card-service__close::before{
                background-color: $gray;
            }
        }
        
        &:nth-of-type(3){
            background-color: $blue;
            top: 180px;
            left: 0;
            z-index: 2;
            height: 370px;
        }

        
        &.is-active{
            z-index: 12;
            background-color: #fff;
            color: $gray;
        }
    }
}