@import "@theme/vars.scss";

.footer {
    overflow: hidden;
    box-sizing: border-box;
    background-color: #151515;
    background-size: 100%;
    background-repeat: no-repeat;
    padding: 56px 0 18px;

    &__nav{
        margin-bottom: 100px;

        .nav{
            width: 100%;
            column-count: 2;
            column-gap: 10px;
            margin-bottom: -20px;

            &__link{
                display: flex;
                color: #fff;
                margin-bottom: 20px;
                transition: color 0.35s ease;

                &:hover{
                    color: $yellow;
                }
            }

            &__link-title{
                flex-shrink: 0;
                font-size: 22px;
            }
        }
    }

    &__contacts{
        display: flex;
        flex-direction: column;
        padding: 32px 20px;
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        margin-right: -20px;
        margin-left: -20px;
    }

    &__contacts-title{
        color: #fff;
        font-size: 22px;
        margin-bottom: 15px;
    }

    &__metro{
        display: inline-flex;
        align-items: center;
        color: $red;
        margin-bottom: 5px;

        img{
            object-fit: contain;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            margin-right: 5px;
        }
    }

    &__contacts-links{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__contacts-link{
        display: inline-flex;
        text-transform: none;
        margin-bottom: 10px;
    }


    &__rights{
        margin-top: 16px;
    }

    &__rights-text{
        color: #fff;
        font-size: 15px;
    }
}


.nav{

    &__link{
        display: inline-flex;
        align-items: center;
        text-decoration: none;
    }
}