@import "@theme/vars.scss";


.section-intro {
    position: relative;
    box-sizing: border-box;
    min-height: 100vh;
    background-color: #151515;
    padding-bottom: 95px;
    z-index: 2;

    &__projects {
        overflow: hidden;
        position: relative;
        height: 600px;
        width: 100%;
        background-color: $gray;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center top;
    }

    &__item {
        position: absolute;
        left: 50%;
        display: block;
        width: 305px;
        height: 178px;
        opacity: 0;

        &:nth-of-type(1){
            width: 275px;
            height: 160px;
            top: 180px;
            transform: translateX(-72px) rotate(15deg);
            z-index: 0;
            transform-origin: right top;
        }

        
        &:nth-of-type(2){
            top: 205px;
            transform: translateX(-228px) rotate(-15deg);
            z-index: 1;
            transform-origin: left top;
        }

        
        &:nth-of-type(3){
            top: 265px;
            transform: translateX(-50%);
            z-index: 2;
            transform-origin: top center;
        }
    }

    &__item-img {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__main {
        position: relative;
        z-index: 2;
        padding: 0 20px;
        margin-top: -54px;
    }

    &__title {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        color: #fff;

        img{
            display: inline-flex;
            height: 42px;
            width: 86px;
            flex-shrink: 0;
            margin-right: 10px;
        }

        span{
            &:first-of-type {
                width: 100%;
            }
        }

        strong{
            width: 100%;
            color: transparent;
            -webkit-text-stroke: 1px #8C8C8C;
        }
    }

    &__button {
        margin-top: 20px;
    }

    &__logo{
        width: 214px;
        height: 214px;
        position: absolute;
        bottom: -107px;
        right: -107px;

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
            animation: rotate linear 40s infinite;
        }
    }
}