@import "@theme/vars.scss";

.project {

    &__intro {
    }

    &__content {
        position: relative;
    }
    
    &__preloader {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        height: 100vh;
        width: 100%;
        pointer-events: none;
        z-index: 2;
    }

    &__preloader--richness &__preloader-logo{
        width: 269px;
        height: 37px;
        bottom: 48px;
        left: 20px;
    }

    &__preloader--olympia &__preloader-logo{
        width: calc(100% - 40px);
        max-width: 440px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__preloader--bonagarden &__preloader-logo{
        width: 156px;
        height: 112px;
        object-fit: contain;
        left: 20px;
        bottom: 44px;
    }
    
    &__preloader--fidesco &__preloader-logo{
        width: 238px;
        height: 84px;
        bottom: 68px;
        left: 20px;
        
    }

    &__preloader--wedu &__preloader-logo{
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 348px;
        aspect-ratio: 1;
        object-fit: contain;
        width: calc(100% - 40px);
        max-width: 440px;
    }

    &__preloader--getman {
        background-color: $gray;
    }

    &__preloader--getman &__preloader-logo{
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 392px;
        object-fit: cover;
    }

    &__preloader-main{
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top;
    }

    &__preloader-logo{
        display: block;
        position: absolute;
        z-index: 1;
        opacity: 0;
    }

    &__main{
        overflow: hidden;
        position: relative;
    }
}

.project-intro {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 120px 20px 0;
    height: 100vh;
    color: #fff;
    background-position: center top;
    background-size: cover;
    background-repeat: no-repeat;

    &__inner{
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__title {
        min-height: 80px;
        flex-shrink: 0;
        color: #fff;
        display: flex;
        flex-direction: column;
        text-align: right;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 50px;
        line-height: 40px;
        margin-bottom: 30px;

        span{
            color: transparent;
            -webkit-text-stroke: 1px #fff;
        }
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 0px auto;
    }

    &__info-item {
        margin: 0 20px 20px;
    }

    &__info-text {
        margin-bottom: 10px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__button {
        margin: 20px auto 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        flex-shrink: 0;
        background-color: #fff;
        fill: $gray;

        .icon{
            width: 50px;
            height: 50px;
        }
    }

    &__logo {
        display: flex;
        justify-content: center;
        background-color: $gray;
        margin-top: 45px;
        width: 100%;
        max-height: 330px;
        overflow: hidden;

        img{
            display: block;
            object-fit: contain;
            max-width: 100%;
            width: 100%;
            max-height: 100%;
        }
    }

    &--richness{
        background-color: #F2B94B;
        color: $gray;
    }

    &--richness &__title{
        color: #000;
        padding-right: 20px;

        span{
            -webkit-text-stroke: 1px #000;
        }
    }


    &--bonagarden{
        background-color: #0FA068;
    }

    
    // &--bonagarden &__title{
    //     color: #000;
    //     padding-right: 20px;

    //     span{
    //         -webkit-text-stroke: 1px #000;
    //     }
    // }

    &--bonagarden &__button{
        fill: #fff;
        background-color: #F08365;
    }

    &--bonagarden &__logo{
        background-color: #fff;
    }

    &--getman{
        background-color: #262626;
    }

    // &--getman &__title{
    //     color: #000;
    //     padding-right: 20px;

    //     span{
    //         -webkit-text-stroke: 1px #000;
    //     }
    // }

    &--getman &__button{
        background-color: #FF7B31;
        fill: #fff;
    }

    &--getman &__logo{
        background-color: #595959;
    }

    &--wedu{
        background-color: #7C4DC2;
    }

    &--wedu &__title{
        flex-direction: row;
        color: #F2B94B;

        span{
            -webkit-text-stroke: 1px #F2B94B;
        }
    }

    &--wedu &__button{
        background-color: #fff;
        fill: #F2B94B;
    }
    
    &--wedu &__logo{
        background-color: #F2B94B;
    }

    &--olympia{
        background-color: #262626;
    }

    &--olympia &__title{
        text-align: left;

        span{
            margin-left: 164px;
            -webkit-text-stroke: 1px #D95B4F;
        }
    }

    &--olympia &__button{
        background-color: #D95B4F;
        fill: #fff;
    }
    
    &--olympia &__logo{
        background-color: #B84236;
    }
    

    &--fidesco{
        background-color: #003399;
    }

    &--fidesco &__logo{
        background-color: #fff;
    }

    &--fidesco &__button{
        background-color: #0D7DFC;
        fill: #fff;
    }

    &--fidesco &__title{
     
    }
}