@import "@theme/vars.scss";

.card-project {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: $gray;

    &.is-visible &__img{
        -webkit-clip-path: inset(0% 0% 0% 0%);
        clip-path: inset(0% 0% 0% 0%);
    }

    &.is-visible &__logo{
        transform: translateY(0);
        opacity: 1;
    }

    &:hover &__img-wrapper {
        &::after{
            opacity: 1;
        }
    }

    &:hover &__logo{
        transform: scale(105%)
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        position: relative;
        width: 100%;
        height: 280px;
        margin-bottom: 40px;
        
        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            backdrop-filter: blur(2px);
            opacity: 0;
            transition: 0.5s ease;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        overflow: hidden;
        object-position: top left;
        transform-origin: left;
        -webkit-clip-path: inset(0% 100% 100% 0%);
        clip-path: inset(0% 100% 100% 0%);
        transition: clip-path 0.75s ease, -webkit-clip-path 0.75s ease;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__logo {
        position: absolute;
        bottom: 24px;
        width: 200px;
        height: 108px;
        left: -48px;
        z-index: 2;
        transform: translateY(-20px);
        opacity: 0;
        transition: transform 0.9s ease, opacity 0.75s ease;
    }

    &:nth-of-type(6n - 4) &__logo{
        left: auto;
        top: auto;
        right: 25px;
        bottom: -24px;
        width: 148px;
        height: 80px;
    }

    &:nth-of-type(6n - 2) &__logo{
        left: auto;
        right: 60px;
        top: 50%;
    }

    &:nth-of-type(6n - 1) &__logo{
        left: -15px;
    }
    &:nth-of-type(6n) &__logo{
        left: -15px;
    }

    &__info {
    }

    &__title {
        line-height: 18px;
        text-transform: initial;
        margin-bottom: 5px;
    }

    &__text {
        line-height: 21px;
    }
}