@import "@theme/vars.scss";

.card-service {
    box-sizing: border-box;
    padding: 30px;
    background-color: $red;
    color: #fff;
    border: 0.5px solid $gray;
    transition: background-color 0.35s ease, color 0.35s ease;

    &.is-active{
        background-color: #fff;
        color: $gray;
    }

    &.is-active &__close{
        &::after{
            background-color: $gray;
            transform: translate(-50%, -50%);
        }

        &::before{
            background-color: $gray;
        }
    }

    &.is-active &__body,
    &.is-active &__button {
        opacity: 1;
        animation: fadeInTop 0.5s ease forwards;
    }

    &__header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 35px;
    }

    &__title {
        line-height: 23px;
    }

    &__close {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 22px;
        height: 22px;
        flex-shrink: 0;
        margin-left: 20px;

        &::before,
        &::after {
            content: "";
            width: 100%;
            height: 1px;
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: background-color 0.35s ease, transform 0.35s ease;
        }


        &::after{
            transform: translate(-50%, -50%) rotate(90deg);
        }
    }

    &__body {
        opacity: 0;
        transition: opacity 0.35s ease;
    }

    &__body-content {
    }

    &__text {
    }

    &__button {
        opacity: 0;
        display: inline-flex;
        align-items: center;
        margin-top: 35px;
        transition: opacity 0.35s ease;

        .icon{
            width: 30px;
            height: 30px;
            margin-left: 10px;
        }
    }

    &__img {
        pointer-events: none;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        max-height: 150px;
    }
}