@import '@theme/vars.scss';
@import '@theme-mixins/media.scss';

.form {

  &__groups {
    display: flex;
    flex-wrap: wrap;
    margin: -20px 0 0 -30px;
  }

  &__group {
    flex-basis: 100%;
    margin: 20px 0 0 30px;
  }

  &__input {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 0 10px;
    border: solid #8C8C8C;
    border-width: 0 0 1px;
    background-color: transparent;
    font-family: "IBMPlexMono";
    font-weight: 400;
    font-size: 18px;
    line-height: 145%;
    color: #F5F5F5;
    outline: none;
    transition: 0.5s ease-out;

    &:focus,
    &:hover {
      border-color: #F5F5F5;
    }

    &::placeholder {
      font-family: "IBMPlexMono";
      font-weight: 400;
      font-size: 18px;
      line-height: 145%;
      letter-spacing: 0.01em;

      color: #BFBFBF;
    }

    &.has-error {
      border-color: #D95B4F;
    }
  }

  &__footer {
    margin-top: 30px;
  }

  &__notice {
    margin: 20px 0px 0;
    font-family: "IBMPlexMono";
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;   
    text-align: center;
    color: #595959;
  }

  &__link {
    color: #F08F3B;
    transition: 0.5s ease-out;

    &:active,
    &:hover {
      opacity: 0.64;
    }
  }
}

.form_theme_dark {

  .form {

    &__input {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0 0 10px;
      border: solid #8C8C8C;
      border-width: 0 0 1px;
      background-color: transparent;
      font-family: "IBMPlexMono";
      font-weight: 400;
      font-size: 18px;
      line-height: 145%;
      color: #262626;
      outline: none;
      transition: 0.5s ease-out;
  
      &:focus,
      &:hover,
      &.filled {
        border-color: #262626;
      }
  
      &::placeholder {
        font-family: "IBMPlexMono";
        font-weight: 400;
        font-size: 18px;
        line-height: 145%;
        letter-spacing: 0.01em;
  
        color: #BFBFBF;
      }

      &.has-error {
        border-color: #D95B4F;
      }
    }
  }
}

.has-error {

  .form__input {
    border-color: #D95B4F;
  }
}
