@import "@theme/vars.scss";

.getman {
    background-color: #313131;
    color: #BFBFBF;
    padding-bottom: 100px;
    font-family: 'IBMPlexMono';

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 160px 20px 90px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__intro-logo {
        width: 284px;
        height: 50px;
        margin-bottom: 20px;
    }

    &__intro-subtitle {
        line-height: 150%;
        margin-bottom: 30px;
    }

    &__intro-info {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        margin-bottom: -20px;
    }

    &__intro-item {
        margin-bottom: 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        margin-bottom: 10px;
        color: #F08F3B;
        margin-bottom: 10px;
    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: -8px center;
    }

    &__screen {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 60px 20px 0;

        &--typography{
            background-color: #fff;
            color: $gray;
        }

        &--1{
            
            &::after{
                content: "";
                width: 100%;
                height: 64px;
                background-color: #fff;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }

        &--2 {
            padding-right: 0;
            padding-left: 0;
            background-color: #fff;
        }

        &--3{
            overflow: hidden;
            align-items: center;
            padding-right: 0;
            padding-left: 0;
            background-color: #121212;
            
        }
    }

    &__screen--typography &__haeder{
        // color: $gray;
    }

    &__screen--1 &__img-wrapper{
        width: 100%;
        z-index: 2;
    }

    &__screen--1 &__img{
        width: 124.5%;
        margin: -11% -13% -14% -13%;
        object-fit: contain;
    }

    &__screen--2 &__header{
        padding: 0 20px;
        margin-bottom: 72px;
    }

    &__screen--2 &__img-wrapper{
        overflow: hidden;
        box-sizing: border-box;
        display: block;
        position: relative;
        width: 50%;

        &--1{
            float: left;
            background-color: #FF7B31;
            z-index: 4;
        }

        &--2{
            float: right;
        }

        &--3{
            float: right;
            background-color: #121212;
        }
    }

    &__screen--2 &__img{
        // box-sizing: border-box;
        // width: 100%;
        // max-height: 100vh;
        // margin-bottom: -18%;
        width: 100%;
        object-fit: contain;

        &--4{
            width: 85%;
            position: absolute;
            right: 0;
            bottom: 0;
            transform: translate(18%, 3%);
            z-index: 2;
        }

    }

    &__screen--2 &__screen-inner{
        display: block;


        &::after{
            content:"";
            display: block;
            clear: both;
        }
    }
    
    &__screen--3 &__header{
        padding: 0 20px;
    }

    &__screen--3 &__screen-inner{
        display: flex;
        justify-content: flex-end;
        position: relative;
        padding-top: 100px;
        margin-top: -60px;
        background-color: #313131;
        width: 100%;
        box-sizing: border-box;
    }

    &__screen--3 &__img-wrapper{
        width: 100%;
        flex-shrink: 0;
        flex-direction: column;
        align-items: flex-end;
    }

    &__screen--3 &__img{
        flex-shrink: 0;
        transform-origin: top left;

        &--1{
            transform-origin: top center;
            max-width: calc(100% - 20px);
            z-index: 2;
        }

        &--2{
            width: 77%;
            flex-shrink: 0;
            margin-bottom: 36px;
        }

        &--3{
            width: 54%;
            margin-bottom: 36px;
        }

        &--4{
            width: 68%;
        }

        &--5{
            transform-origin: top right;
            flex-shrink: 0;
            width: 35.8%;
            margin-left: 40px;
        }
    }

 
    &__img-wrapper{
        display: flex;
        // border: 2px solid blue;
    }

    &__img {
        // border: 2px solid red;
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
    }

    &__header {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 30px;
        position: relative;
        z-index: 2;
        width: 100%;
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__typography-img {
        display: block;
        width: calc(100% - 30px);
        max-width: 290px;
        object-fit: contain;
    }

    &__typography-colors {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 -10px 10px;
    }

    &__typography-color {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 10px 20px;
        color: $gray;

        img{
            display: block;
            max-width: 148px;
            flex-shrink: 0;
            margin-bottom: 18px;
        }
    }

}
