@import '@theme/vars.scss';


.slider {
    height: max-content;

    &__button {
        margin: 0;
        box-sizing: border-box;
        position: static;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 2px solid $blue;
        fill: $blue;
        background-color: transparent;
        transition: background-color 0.35s ease, fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

        &:hover{
            background-color: $blue;
            color: #fff;
            fill: #fff;
        }

        &::after, 
        &::before{
            content: none;
            
        }
    }

    &__button-icon{
        flex-shrink: 0;
        width: 32px;
        height: 32px;
    }
      
    &__pagination {
        position: static;
        display: flex;
        align-items: center;
        margin-top: 120px;
        background-color: rgba(195, 79, 68, 0.2);
    }

    &__pagination-item {
        display: flex;
        height: 2px;
        flex-grow: 1;
        margin: 0;
        background-color: transparent;
        background-color: $red;
        transform: scaleX(0);
        transition: transform 0.5s ease;

        &.swiper-pagination-bullet-active{
            transform: scaleX(1);
            transition: transform 0.35s ease 0.3s;
        }
    }
}