@import "@theme/vars.scss";

.fidesco {
    background-color: #fff;
    color: $gray;
    padding-bottom: 60px;
    font-family: 'Roboto';

    &__intro {
        display: flex;
        flex-direction: column;
        height: 100vh;
        position: relative;
        color: #fff;
        background-color: #003399;
        overflow: hidden;
    }

    &__intro-inner{
        box-sizing: border-box;
        padding: 100px 20px 60px;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        // flex-grow: 1;
    }

    &__intro-logo {
        width: calc(100% - 20px);
        max-width: 440px;
        margin-bottom: 40px;
    }

    &__intro-subtitle {
        text-align: center;
        margin-bottom: 30px;
        line-height: 132%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -15px -20px;
    }

    &__intro-item {
        display: flex;
        flex-direction: column;
        margin: 0 15px 20px;

        a{
            color: #fff;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        font-size: 11px;
        line-height: 13px;
        margin-bottom: 16px;
    }

    &__intro-item-text{
        font-size: 11px;
        line-height: 13px;
        display: flex;
    }

    &__intro-bg{
        display: block;
        flex-grow: 1;
        width: 100%;
        object-fit: cover;
    }

    &__screen {
        display: flex;
        flex-direction: column;
        position: relative;
        padding: 60px 20px 0;

        &--typography{
        }

        &--1{
            padding-right: 0;
            padding-left: 0;
            overflow: hidden;
        }
    }

    &__screen--1 &__screen-inner{
        position: relative;
        display: flex;
        justify-content: center;
        width: 100%;

        &--1{
            margin-bottom: 40px;
            min-height: 260px;
        }

        &--2{
           min-height: 455px;
        }
    }

    &__screen--1 &__img-wrapper{
        display: flex;
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 1;
        transform: translate(-50%, -50%);
        max-width: 480px;

        &--1{
            width: calc(100% - 40px);
        }

        &--2{
            width: calc(100% - 20px);
        }
    }
    
    &__screen--1 &__img{

        &--1{
            width: 100%;
            object-fit: cover;
            object-position: center;
            max-height: 400px;
        }

        &--2{
            width: 141.5%;
            margin: -12% -24% -32% -18%;
        }

        &--3{
            width: 100%;
            object-fit: cover;
            object-position: center;
            max-height: 440px;
        }

        &--4{
            width: 110%;
            margin: -3% -5% -10% -5.5%;
        }
    }

    &__screen--2 &__header{
        margin-bottom: 30px;
    }
    
    &__screen--2 &__img-wrapper{
        width: 70%;
        max-width: 360px;
        margin-bottom: 10px;

        &:last-of-type{
            margin-bottom: 0;
        }

        &--1{

        }

        &--2{
            margin-left: auto;
        }

        &--3{

        }

    }
    
    &__screen--2 &__img{
        width: 166%;
        object-fit: contain;
        margin: -5% -31% -62% -35%;

        &--1{

        }

        &--2{
        }

        &--3{

        }
    }


    &__img-wrapper{
        display: flex;
        position: relative;
    }

    &__img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
        z-index: 1;
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 40px;
    }

    &__header-title {
        text-transform: uppercase;
        font-size: 22px;
        line-height: 26px;
    }

    &__header-number {
        position: relative;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        width: 60px;
        height: 60px;
        margin-right: 15px;
        background-size: 100%;
        background-repeat: no-repeat;
        background-image: url("data:image/svg+xml,%3Csvg width='59' height='59' viewBox='0 0 59 59' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect y='29.5' width='41.7193' height='41.7193' rx='3.65959' transform='rotate(-45 0 29.5)' fill='white'/%3E%3Crect x='8.7832' y='30.0085' width='29.2767' height='29.2767' rx='3.65959' transform='rotate(-45 8.7832 30.0085)' fill='%230D7DFC'/%3E%3C/svg%3E%0A");

        span{
            position: relative;
            z-index: 2;
        }
        &::before{
            content: "";
            width: 42px;
            height: 42px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(-45deg);
            box-shadow: -1.31654px 25.5329px 25.5256px rgba(45, 135, 219, 0.08), 0.731918px 0px 9.51493px rgba(45, 135, 219, 0.12), -0.731918px 2.19575px 3.38512px rgba(45, 135, 219, 0.08);
        }
    }

    &__typography {
        display: flex;
        flex-direction: column;
    }

    &__typography-img {
        display: block;
        width: 80%;
        max-width: 392px;
        object-fit: contain;
        margin-bottom: 30px;
    }

    &__typography-colors {
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 358px;
    }

    &__typography-color {
        box-sizing: border-box;
        position: absolute;
        display: flex;
        color: #fff;
        border-radius: 15px;
        flex-shrink: 0;
        padding: 30px;
        box-shadow: -1.79875px 34.8849px 34.875px rgba(45, 135, 219, 0.08), 1px 0px 13px rgba(45, 135, 219, 0.12), -1px 3px 4.625px rgba(45, 135, 219, 0.08);
        color: #fff;

        span{
   
        }

        &:nth-of-type(1){
            width: 104%;
            height: 234px;
            top: 0;
            left: 48px;
            top: 0;
            background-color: #fff;
            color: #003399;
            border: 1px solid rgba(0, 51, 153, 0.15);
        }

        &:nth-of-type(2){
            width: 84%;
            height: 202px;
            background-color: #003399;
            left: 0;
            top: 92px;            
        }

        &:nth-of-type(3){   
            height: 168px;
            width: 74%;
            background-color: #0D7DFC;
            top: 192px;
            left: 96px;
        }

    }
}
