@import "@theme/vars.scss";

.section-projects {
    overflow: hidden;
    padding-bottom: 0;

    &__list {
    }

    &__item {
        margin-bottom: 40px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__banner {
        display: flex;
        align-items: center;
        padding: 20px 0;
        margin-top: 60px;
        flex-shrink: 0;
        background-color: $red;
    }

    &__banner-text{
        text-transform: uppercase;
        color: #fff;
        flex-shrink: 0;
        animation: projectsBanner 30s linear infinite;
        padding: 0 10px;
    }
}
