@import "@theme/vars.scss";
@import "@theme/animations.scss";

.header {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 10;
    width: 100%;
    padding: 10px 0;
    transition: background-color 0.5s ease;

    &.is-fixed{
        background-color: rgba(0, 0, 0, 0.35);
        backdrop-filter: blur(3px);
    }

    &.is-fixed &__logo-main{
        opacity: 0;
        transition-delay: 0s;
    }

    &.is-fixed &__logo-simple{
        opacity: 1;
        transition-delay: 0.35s;
    }

    &.is-active{
        background-color: #fff;
    }

    &.is-active &__logo-main{
        opacity: 1;
        transition-delay: 0.35s;
    }

    &.is-active &__logo-simple{
        opacity: 0;
        transition-delay: 0s;
    }

    &__container {
        position: relative;
        z-index: 1;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 55px;
        height: 47px;

        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__logo-main{
        opacity: 1;
        transition: opacity 0.35s ease-in-out 0.35s;
    }

    &__logo-simple{
        opacity: 0;
        transition: opacity 0.35s ease-in-out;
    }

    &__burger {
        cursor: pointer;
        position: relative;
        width: 40px;
        height: 40px;
        flex-shrink: 0;
        border-radius: 50%;
        transition: border-color 0.35s ease, transform 0.65s ease;

        &::before,
        &::after,
        span{
            width: 25px;
            height: 2px;
            position: absolute;
            background-color: #fff;
            left: 50%;
            top: 50%;
            transform-origin: center;
            transition: transform 0.65s ease, opacity 0.5s ease, background-color 0.35s ease;
        }
        
        &::before{
            content: "";
            transform: translate(-50%, calc(-50% - 6px));
        }
        
        span{
            transform: translate(-50%, -50%) rotate(0) scaleX(1);
        }

        &::after{
            content: "";
            transform: translate(-50%, calc(-50% + 6px));
        }

        &.is-active{

            &:before{
                transform: translate(-50%, -50%) rotate(-50deg);
                background-color: #BFBFBF;
            }

            &:after{
                transform: translate(-50%, -50%) rotate(50deg);
                background-color: #BFBFBF;
            }

            span{
                transform: translate(-50%, -50%) rotate(90deg) scaleX(0);
                opacity: 0;
            }
            
            &:hover{
                &::before,
                &::after,
                span{
                    background-color: $red;
                }
            }
        }
    }

    &__content {
        box-sizing: border-box;
        display: block;
        position: absolute;
        top: 100%;
        left: 0;
        visibility: hidden;
        opacity: 0;
        height: calc(100vh - 67px);
        background-color: #fff;
        width: 100%;
        padding: 22px 20px 32px;

        transition: visibility 0.5s ease, opacity 0.5s ease;

        &.is-active{
            visibility: visible;
            opacity: 1;
        }
    }

    &__content-inner {
        display: flex;
        flex-direction: column;
        max-height: 100%;
        overflow: hidden auto;

        scrollbar-color: transparent transparent;
        
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }
        
    }

    &__menu {
        display: flex;
        margin-bottom: 10px;
    }

    &__content-title {
        color: #8C8C8C;
    }

    &__nav {
        padding-top: 40px;

        .nav{
            display: flex;
            flex-direction: column;

            &__link{
                margin-bottom: 15px;
                color: $gray;
                transition: color 0.35s ease;

                &:hover{
                    color: $yellow;
                }
            }

            &__link-title{
                display: inline-flex;
                align-items: flex-start;

                span{
                    font-size: 16px;
                    line-height: 145%;
                    color: $red;
                    margin-left: 5px;
                }

                strong{
                    color: $red;
                }
            }
        }
    }

    &__projects {
        display: flex;
        flex-direction: column;
    }

    &__projects-title {
        color: #8C8C8C;
        margin-bottom: 20px;
    }

    &__project {
        display: block;
        text-decoration: none;
        color: $gray;
        width: 100%;
        margin-bottom: 20px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__project:hover &__project-img::after{
        opacity: 1;
    }

    &__project-img {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 10px;
        overflow: hidden;
        
        &::after{
            content: "";
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: transparent;
            backdrop-filter: blur(4px);
            opacity: 0;
            transition: opacity 0.5s ease;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            aspect-ratio: 1.6;
            max-height: 280px;
        }
    }

    &__project-info {
    }

    &__project-title {
        text-transform: initial;
        line-height: 18px;
    }

    &__project-text {
        line-height: 21px;
    }
}