@import "@theme/vars.scss";

button,
a {
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    z-index: 1;
    cursor: pointer;
    padding: 0;
    background: none;
    outline: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    box-sizing: border-box;
    padding: 0 30px;
    border: 1px solid #000;
    border-radius: 10px;
    font-family: 'IBMPlexMono';
    text-decoration: none;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    color: $gray;
    fill: $gray;
    background-color: #fff;
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease;

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 30px;
        height: 30px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon &__text{
        margin-left: 10px;
    }

    &:hover{
        background-color: $gray;
        border-color: $gray;
        fill: #fff;
        color: #fff;
    }

    &[disabled]{
        pointer-events: none;
        opacity: 0.5;
    }


    // &--border{
    //     color: $yellow;
    //     fill: $yellow;
    //     background-color: transparent;

    //     &:hover{
    //         background-color: $yellow;
    //         border-color: $yellow;
    //         color: #fff;
    //         fill: #fff;
    //     }
    // }

    &--gray{
        background-color: $gray;
        border-color: $gray;
        fill: #fff;
        color: #fff;

        &:hover{
            color: $gray;
            fill: $gray;
            background-color: #fff;
            border-color: #fff;
        }
    }

    &--border#{&}--white{
        border-color: #fff;
        fill: #fff;
        color: #fff;
        background-color: transparent;

        &:hover{
            border-color: #fff;
            background-color: #fff;
            color: $yellow;
            fill: $yellow;
        }
    }

    &--icon{
        padding: 0;
        width: 54px;
    }

    // input{
    //     display: none;
    // }
}


.button_type_secondary {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 10px;
    color: #fff;
    background-color: #262626;
  
    &:active .button__icon,
    &:hover .button__icon {
      transform: translateX(8px);
    }
  
    // &:active .button__text,
    // &:hover .button__text {
    //   opacity: 0.8;
    // }
  
    .button__icon {
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin: 0 0 0 15px;
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center;
        transition: 0.5s ease-out;
        background-color: #fff;
        background-position: center 1px;
        background-image: url("data:image/svg+xml,%3Csvg width='31' height='30' viewBox='0 0 31 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M24.1036 14.3536C24.2988 14.1583 24.2988 13.8417 24.1036 13.6464L20.9216 10.4645C20.7263 10.2692 20.4097 10.2692 20.2145 10.4645C20.0192 10.6597 20.0192 10.9763 20.2145 11.1716L23.0429 14L20.2145 16.8284C20.0192 17.0237 20.0192 17.3403 20.2145 17.5355C20.4097 17.7308 20.7263 17.7308 20.9216 17.5355L24.1036 14.3536ZM6.75 14.5H23.75V13.5H6.75V14.5Z' fill='%23262626'/%3E%3C/svg%3E%0A");
    }
  }