@import "@theme/vars.scss";

.card-product {
    box-sizing: border-box;
    position: relative;
    cursor: pointer;
    text-decoration: none;
    color: $gray;
    display: flex;
    padding: 30px 28px;
    border-radius: 5px;
    background: #FFFFFF;
    box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
    transition: 0.5s ease;
    opacity: 0;
    font-family: 'IBMPlexMono';
    transform: translateY(30px) scale(0.9);
    transform-origin: top center;

    &:hover{
        box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.02), 0px 6px 16px rgba(0, 0, 0, 0.02), 0px 3px 6px -4px rgba(0, 0, 0, 0.3);
    }

    &:hover &__img{
        transform: scale(110%);
    }
    
    &:hover &__button{
        fill: $red;
    }

    &.is-visible{
        opacity: 1;
        transform: translateY(0) scale(1);
    }

    &__inner{
        display: flex;
        flex-direction: column;
    }
    

    &__title {
        display: inline-flex;
        font-size: 22px;
        line-height: 29px;
        align-items: center;
        margin-bottom: 20px;
        color: $gray;
        fill: $red;

        .icon{
            flex-shrink: 0;
            width: 35px;
            height: 21px;
            margin-right: 5px;
        }
    }

    &__img-wrapper {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 20px;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        aspect-ratio: 1.95;
        max-height: 200px;
        transition: 0.75s ease;
    }

    &__text {
        display: inline-flex;
        align-items: center;
        margin-bottom: 10px;
    }

    &__options {
    }

    &__option {
        color: $gray-light;
        margin-bottom: 10px;

        &:last-of-type{
            margin-bottom: 15px;
        }
    }

    &__deadline {
        text-transform: lowercase;
        color: $black;
    }

    &__price {
        text-transform: lowercase;
        color: $red;
        margin: 12px 0;
    }

    &__button {
        position: absolute;
        bottom: 30px;
        right: 38px;
        width: 50px;
        height: 50px;
        fill: $black;
        transition: fill 0.35s ease;
    }
}