@import "@theme/vars.scss";

.wedu {
    background-color: #fff;
    color: $gray;
    font-family: 'IBMPlexMono';

    &__intro {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
        padding: 30px 20px;
        position: relative;
    }

    &__intro-inner{
        box-sizing: border-box;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 50px 30px 30px;
        border-radius: 50%;
        width: 100%;
        max-width: 440px;
        aspect-ratio: 1;
        border: 3px solid #7C4DC2;
        background-color: #F2B94B;
    }

    &__intro-logo {
        width: 202px;
        height: 155px;
        margin-bottom: 10px;
    }

    &__intro-subtitle {
        text-align: center;
        margin-bottom: 16px;
        line-height: 150%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8 -12px;
    }

    &__intro-item {
        margin: 0 8px 12px;

        a{
            display: flex;
            color: inherit;
        }
    }

    &__intro-item-title {
        display: flex;
        font-size: 10px;
        line-height: 13px;
        color: #7C4DC2;
        margin-bottom: 6px;
    }

    &__intro-item-text{
        font-size: 9px;
        line-height: 12px;
    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: flex;
        flex-direction: column;

        &--typography{
            align-items: center;
            background-color: $gray;
            background-size: 100px 100px, cover;
            background-repeat: repeat, no-repeat;
            background-position: top left, top center;
            padding: 35px 20px 150px;
            min-width: 264px;
        }

        &--1{
            align-items: center;
            margin-top: -100px;
            padding: 0 20px 0;
            z-index: 1;

            &::after{
                content:"";
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 90px;
                background-color: #7C4DC2;
            }
        }

        &--2 {
            align-items: center;
            padding: 45px 0;
            background-color: #7C4DC2;
        }

        &--3{
            flex-direction: row;
            height: 67vh;
            min-height: 565px;
            overflow: hidden;
        }

        &--4{
            background-size: 100px 100px, cover;
            background-repeat: repeat, no-repeat;
            background-position: top left, top center;
            padding: 60px 0;
        }
    }

    &__screen--typography &__haeder{
        margin-bottom: 150px;
    }

    &__screen--1 &__img-wrapper{
        width: 100%;
        max-width: 540px;
    }

    &__screen--1 &__img{
        width: 126%;
        margin: -12% -13% -15% -13%;
    }

    &__screen--2 &__img-wrapper{
        
        &--1{
            width: 78%;
            min-width: 320px;
            max-width: 520px;
            aspect-ratio: 1;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &--2{
            width: 45%;
            min-width: 175px;
            max-width: 320px;
            z-index: 2;
        }
    }

    &__screen--2 &__img{
        object-fit: contain;

        &--1{
            width: 100%;
            display: block;
            animation: rotate 40s linear infinite;
        }

        &--2{
            width: 148%;
            margin: -10% -50% -43% -2.5%;
        }
    }

    &__screen--3 &__img{
        height: 100%;
        width: auto;
        min-width: 130%;
        object-fit: cover;
    }

    &__screen--4 &__img{
        object-fit: contain;
        width: 82%;

        &--1{
            transform: translateX(-14.4%);
        }

        &--2{
            margin-top: -30px;
            margin-left: auto;
            transform: translateX(25%);
        }
    }
    
        // border: 2px solid black;
        // border: 2px solid red;


    &__img-wrapper{
        display: flex;
        position: relative;
        z-index: 1;
    }

    &__img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
    }

    &__header {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        padding: 0 20px;
        margin-bottom: 100px;
        position: relative;
        z-index: 2;
        width: 100%;
    }

    &__header-title {
    }

    &__header-number {
    }

    &__typography {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 76%;
        min-width: 268px;
        max-width: 392px;
    }

    &__typography-title{
        display: flex;
        object-fit: contain;
        width: 262px;
        height: 32px;
        margin-bottom: 60px;
    }

    &__typography-img {
        display: block;
        width: 100%;
        object-fit: contain;
    }

    &__typography-colors {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px 24px;
        margin-bottom: 40px;
    }

    &__typography-color {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $gray;
        margin: 0 8px 16px;

        img{
            display: block;
            width: 76px;
            height: 76px;
            flex-shrink: 0;
            margin-bottom: 10px;
        }

        span{
            font-size: 13px;
            line-height: 18px;
            color: #D9D9D9;
        }
    }
}