@import "@theme/vars.scss";

.section-about {
    overflow: hidden;
    background-color: $dark;
    color: #fff;

    &__statistics {
        margin-bottom: 30px;
    }

    &__statistics-img {
        width: 100%;
        aspect-ratio: 2;
        object-fit: contain;
        margin-bottom: 30px;
    }

    &__statistics-list {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin: 0 -10px;
    }

    &__statistics-item {
        display: flex;
        flex-direction: column;
        display: block;
        margin: 0 10px;
        flex: 1 1 calc(33.33% - 20px);

        .h1{
            line-height: 35px;
            margin-bottom: 10px;
        }

        span{
            display: inline-flex;
            color: #BFBFBF;
        }
    }

    &__services {
    }

    &__services-title {
        text-transform: initial;
        margin-bottom: 20px;
    }

    &__services-slider {

        .slider{

            &__inner{
                overflow: visible;
            }

            &__wrapper {
            }

            &__slide {
            }
        }
    }

    &__service-slide {
        width: 70%;
    }

    &__swervice-slide.swiper-slide-active &__service{
        box-shadow: 0px 9px 132px 8px rgba(54, 51, 51, 0.25), 0px 6px 16px rgba(54, 51, 51, 0.1), 0px 3px 6px -4px rgba(54, 51, 51, 0.1);
    }
    
    &__service-slide--white &__service{
        background-color: #fff;
    }

    &__service-slide--yellow &__service{
        background-color: $yellow;

        color: $gray;
        
        .link{
            color: $gray;
            fill: $gray;
            border-color: $gray;
        }
    }

    &__service-slide--red &__service{
        background-color: $red;  
        color: #fff;
        
        .link{
            color: #fff;
            fill: #fff;
            border-color: #fff;
        }
    }

    &__service-slide--blue &__service{
        background-color: $blue;

        color: #fff;
        
        .link{
            color: #fff;
            fill: #fff;
            border-color: #fff;
        }
    }

    
    &__service {
        box-sizing: border-box;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        min-height: 375px;
        padding: 45px 18px;
        border-radius: 10px;
        background-color: #fff;
        color: $gray;
        text-decoration: none;
        transition: box-shadow 0.5s ease;
    }

    &__service:hover &__service-link{
        border-color: transparent;
    }

    &__service-main {
    }

    &__service-title {
        text-transform: initial;
    }

    &__service-link {

        max-width: 100%;
        
        span{
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    &__service-logo {
        pointer-events: none;
        position: absolute;
        z-index: 1;
        max-height: 25px;

        &:nth-of-type(1){
            top: 10px;
            left: 10px;
        }
        
        &:nth-of-type(2){
            top: 10px;
            right: 10px;
        }
        
        &:nth-of-type(3){
            bottom: 10px;
            left: 10px;
        }
        
        &:nth-of-type(4){
            bottom: 10px;
            right: 10px;
        }
    }
}
