@import '@theme/vars.scss';

.link{
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    color: $yellow;
    fill: $yellow;
    border-color: $yellow;
    transition: color 0.25s ease, fill 0.25s ease, border-color 0.25s ease;

    span{
        border-bottom: 1px solid;
        border-color: inherit;
    }

    &-white{
        color: #fff;
        fill: #fff;
        border-color: #fff;
    }
    
    &-red{
        color: $red;
        fill: $red;
        border-color: $red;
    }


    .icon{
        width: 25px;
        height: 25px;
        flex-shrink: 0;
    }

    span + .icon{
        margin-left: 5px;
    }

    .icon + span {
        margin-left: 5px;
    }

    &:hover{
        border-color: transparent; 
    }

}