@import "@theme/vars.scss";

.preloader{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background-color: $gray;

    &.is-loaded{
        transition: opacity 0.5s ease 0.35s, visibility 0.5s ease 0.35s;
        pointer-events: none;
        opacity: 0;
        visibility: hidden;
    }

    &.is-loaded &__main{
        opacity: 0;
        transition: opacity 0.5s ease 0.2s;
    }

    &.is-loaded &__shadow{
        opacity: 0 !important;
        transition: opacity 0.5s ease 0.2s;
    }

    &.is-loaded &__img{
        &--1{
            transform: translate(-50%, -50%) scale(6.2);
            opacity: 0;
            transition: transform 0.9s cubic-bezier(.48,-0.28,.48,1.1) 0.2s, opacity 0.5s ease 0.4s;
        }

        &--2{
            transform: translate(-50%, -50%) rotate(-15deg) scale(4.2);
            opacity: 0;
            transition: transform 0.9s cubic-bezier(.48,-0.28,.48,1.1) 0.1s, opacity 0.5s ease 0.3s;
        }

        &--3{
            transform: translate(-50%, -50%) rotate(30deg) scale(3.2);
            opacity: 0;
            transition: transform 0.9s cubic-bezier(.48,-0.28,.48,1.1), opacity 0.5s ease 0.2s;
        }
    }

    @keyframes preloaderRotate {
        0%{
            transform: rotate(0deg);
        }

        100%{
            transform: rotate(360deg);
        }
    }

    @keyframes preloaderRotateReverse {
        0%{
            transform: rotate(0deg);
        }

        100%{
            transform: rotate(-360deg);
        }
    }

    &__img{
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);


        span{
            display: block;
            width: 100%;
            height: 100%;
            background-size: 100%;
            background-repeat: no-repeat;
            background-position: center;
        }

