@import "@theme/vars.scss";

.bonagarden {
    background-color: #F2F2F2;
    color: $gray;
    padding-bottom: 60px;

    &__intro {
        display: flex;
        flex-direction: column;
        min-height: 80vh;
        position: relative;
        color: #fff;
    }

    &__intro-inner{
        box-sizing: border-box;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        padding-bottom: 210px;
    }

    &__intro-logo-wrapper{
        padding: 120px 0 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
    }

    &__intro-logo {
        margin-bottom: 18px;
        background-color: #fff;
        margin: 0 85px;
        flex-grow: 1;
        max-width: 220px;
        object-fit: contain;
    }

    &__intro-main{
        padding: 52px 20px 0;
    }

    &__intro-subtitle {
        margin-bottom: 30px;
        line-height: 132%;
    }

    &__intro-info {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -17.5px -20px;
    }

    &__intro-item {
        margin: 0 17.5px 20px;

        a{
            color: #fff;
            font-weight: 400;
            text-decoration: underline;
        }
    }

    &__intro-item-title {
        display: flex;
        margin-bottom: 10px;
    }

    &__intro-item-text{

    }

    &__intro-bg{
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__screen {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 60px 20px;

        &--typography{
            background-color: #99C6B5;
        }

        &--1{
            align-items: center;
            margin-top: -180px;
            padding-top: 0;
        }

        &--2 {
            align-items: center;
            padding-right: 0;
            padding-left: 0;
        }

        &--3{
            align-items: center;
            background-color: #99C6B5;
            padding-right: 40px;
            padding-left: 40px;
        }

        &--4{
            padding-bottom: 0;
            padding-right: 0;
            padding-left: 0;
        }

        &--5{
          
        }

        &--6{
 
        }
    }

    &__screen--typography &__header{
        color: #fff;
    }

    &__screen--1 &__img-wrapper{
        display: flex;
        width: 100%;
        max-width: 500px;
    }

    &__screen--1 &__img{
        width: 122%;
        margin-left: -8%;
        margin-right: -14%;
        margin-top: -7%;
        margin-bottom: -20%;
    }

    &__screen--2 &__header{
        padding-right: 20px;
        padding-left: 20px;
        margin-bottom: 92px;
    }

    &__screen--2 &__img-wrapper{
        width: 100%;
        max-width: 520px;
        margin-bottom: 30px;

        &--1{
            transform:translateX(12.5%);
        }

        &--2{
            transform:translateX(-29%);
        }

        &--3{
            transform:translateX(23%);
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__screen--2 &__img{
        width: 142%;
        margin-left: -12%;
        margin-right: -13.8%;
        margin-bottom: -23%;
        margin-top: -9%;
        
        &--1{
            position: absolute;
            top: 65px;
            right: 0;
            width: 118px;
            height: 106px;
            margin: 0;
        }
    }

    &__screen--3 &__img{
        width: 100%;
        max-width: 480px;
        margin-bottom: 20px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__screen--4 &__header{
        padding-right: 20px;
        padding-left: 20px;
        margin-bottom: 86px;
    }
    
    &__screen--4 &__img-wrapper{
        width: 68%;
        max-width: 520px;
        margin-bottom: 30px;

        &--1{
            transform:translateX(5%);
        }

        &--2{
            transform:translateX(2%);
            margin-left: auto;
        }

        &--3{
            transform:translateX(5%);
        }

        &--4{
            transform:translateX(-5%);
            margin-left: auto;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__screen--4 &__img{
        width: 157%;
        margin: -19% -37% -41% -22%;
        
        &--1{
            position: absolute;
            top: 78px;
            right: -4px;
            width: 302px;
            height: 52px;
            margin: 0;
        }
    }

    &__img {
        position: relative;
        display: block;
        object-fit: cover;
        object-position: center top;
    }

    &__header {
        position: relative;
        z-index: 1;
        box-sizing: border-box;
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-bottom: 45px;
    }

    &__header-title {
        font-size: 22px;
        line-height: 29px;
    }

    &__header-number {
    }

    &__typography {
        display: flex;
        flex-direction: column;
    }

    &__typography-title{
        color: #fff;
        line-height: 128%;
        margin-bottom: 45px;
    }

    &__typography-img {
        display: block;
        width: 100%;
        max-width: 392px;
        object-fit: contain;
        margin-bottom: 60px;
    }

    &__typography-colors {
        width: 67%;
        max-width: 320px;
        display: flex;
        flex-wrap: wrap;
    }

    &__typography-color {
        box-sizing: border-box;
        display: flex;
        width: 50%;
        aspect-ratio: 1;
        background-color: #5FA494;
        color: #fff;
        font-family: 'Roboto';
        padding: 10px;

        span{
            margin-bottom: 10px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        &:nth-of-type(1){
            background-color: #F08365;
        }

        &:nth-of-type(2){
        }

        &:nth-of-type(3){   
            background-color: #E6BE80;
            color: $gray;
        }

        &:nth-of-type(4){
            background-color: #fff;
            color: #676767;
        }
    }
}
